body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.admin {
  height: calc(100vh);
  background-color: #f1f3f5;
}
.admin .header {
  margin: 0 auto;
  padding: 0 20px;
  width: 75%;
  line-height: 50px;
  color: #ffffff;
  min-width: 1000px;
}
.admin .header .logo {
  width: 150px;
  height: 50px;
  margin: 0 16px;
  float: left;
  font-style: italic;
  color: aquamarine;
}
.admin .header .logo img {
  height: 35px;
  margin-right: 16px;
}
.admin .header .user {
  float: right;
  color: #ffffff;
}
.content {
  margin: 0 auto;
  width: 75%;
  min-height: 500px;
  padding: 24;
  background: #ffffff;
  min-width: 1000px;
}

.login {
  height: calc(100vh);
  background-color: #f1f3f5;
}
.login .logo {
  height: 50px;
  font-size: 25px;
}
.login .logo img {
  height: 35px;
  margin-right: 16px;
}
.login .logo b {
  vertical-align: middle;
}
.login-box {
  position: absolute;
  width: 400px;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-top: -200px;
  margin-left: -200px;
}

