.admin {
  height: calc(100vh);
  background-color: #f1f3f5;
}
.admin .header {
  margin: 0 auto;
  padding: 0 20px;
  width: 75%;
  line-height: 50px;
  color: #ffffff;
  min-width: 1000px;
}
.admin .header .logo {
  width: 150px;
  height: 50px;
  margin: 0 16px;
  float: left;
  font-style: italic;
  color: aquamarine;
}
.admin .header .logo img {
  height: 35px;
  margin-right: 16px;
}
.admin .header .user {
  float: right;
  color: #ffffff;
}
.content {
  margin: 0 auto;
  width: 75%;
  min-height: 500px;
  padding: 24;
  background: #ffffff;
  min-width: 1000px;
}
